import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ChevronDown } from 'lucide-react';

const faqData = [
  {
    question: 'Como funciona o perodo de teste?',
    answer:
      'Voc pode experimentar o MedBot gratuitamente por 7 dias. Durante este perodo, voc ter acesso a todas as funcionalidades para ver como ele pode te ajudar a se preparar para as provas de residncia.',
  },
  {
    question: 'Preciso ter conhecimento tcnico para usar?',
    answer:
      'Absolutamente no! O MedBot foi projetado para ser intuitivo e fcil de usar. Se voc sabe usar um chat, voc saber usar o MedBot.',
  },
  {
    question: 'As questes so atualizadas?',
    answer:
      'Sim, nossa base de dados  constantemente atualizada com as ltimas questes de provas de residncia de todo o pas, garantindo que seu estudo esteja sempre em dia.',
  },
  {
    question: 'Como posso entrar em contato com o suporte?',
    answer:
      'Nosso suporte est disponvel 24/7. Voc pode entrar em contato atravs do chat dentro da plataforma ou pelo email suporte@medbot.com.',
  },
  {
    question: 'Quais so as formas de pagamento?',
    answer:
      'Aceitamos os principais cartes de crdito (Visa, MasterCard, Amex) e tambm pagamentos via PIX para sua convenincia.',
  },
  {
    question: 'Posso cancelar a qualquer momento?',
    answer:
      'Sim, voc pode cancelar sua assinatura a qualquer momento, sem taxas ou burocracia. O cancelamento  feito diretamente no seu painel de usurio.',
  },
];

const FaqItem: React.FC<{
  item: typeof faqData[0];
  isOpen: boolean;
  onClick: () => void;
}> = ({ item, isOpen, onClick }) => {
  return (
    <div className="group rounded-lg bg-white dark:bg-subtle-dark p-6 shadow-sm ring-1 ring-subtle-light dark:ring-subtle-dark/50 hover:bg-primary/5 dark:hover:bg-primary/10 transition-colors duration-300">
      <button
        onClick={onClick}
        className="flex w-full cursor-pointer list-none items-center justify-between text-left"
      >
        <span className="text-base font-semibold text-content-light dark:text-content-dark">
          {item.question}
        </span>
        <ChevronDown
          className={`h-6 w-6 transform text-primary transition-transform duration-300 ${
            isOpen ? 'rotate-180' : ''
          }`}
        />
      </button>
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ opacity: 0, height: 0, marginTop: 0 }}
            animate={{ opacity: 1, height: 'auto', marginTop: '16px' }}
            exit={{ opacity: 0, height: 0, marginTop: 0 }}
            transition={{ duration: 0.3, ease: 'easeInOut' }}
          >
            <p className="text-base leading-relaxed text-content-light/80 dark:text-content-dark/80">
              {item.answer}
            </p>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

const FAQ: React.FC = () => {
  const [openIndex, setOpenIndex] = useState<number | null>(0);

  return (
    <section id="faq" className="py-20 sm:py-28 bg-background-light dark:bg-background-dark">
      <div className="w-full max-w-4xl px-4 py-8 sm:px-6 lg:px-8 mx-auto">
        <div className="mx-auto max-w-2xl text-center">
          <h2 className="text-3xl font-bold tracking-tight text-content-light dark:text-content-dark sm:text-4xl font-display">
            Dvidas Frequentes
          </h2>
          <p className="mt-4 text-lg leading-8 text-content-light/80 dark:text-content-dark/80">
            Tudo que voc precisa saber antes de comear
          </p>
        </div>
        <div className="mx-auto mt-12 max-w-3xl space-y-4">
          {faqData.map((item, index) => (
            <FaqItem
              key={index}
              item={item}
              isOpen={openIndex === index}
              onClick={() => setOpenIndex(openIndex === index ? null : index)}
            />
          ))}
        </div>
      </div>
    </section>
  );
};

export default FAQ;